import React, {useRef, useState} from 'react';
import { Link } from "gatsby";
import classnames from 'classnames';
import './navigation.component.css';
import {useSummaryVisibility} from "../../hooks/use-summary-visibility";

const NavigationComponent = ({ variant = false }) => {
    const navRef = useRef();
    const [isHidden, setHidden] = useState(false);
    const navClassNames = classnames(
        'custom-nav sticky navbar is-hidden-print',
        {
            'is-hidden': isHidden,
            'is-primary': true,
            'is-variant': variant
        }
    );

    useSummaryVisibility((isHide) => {
        setHidden(isHide);
    })

    return (
        <nav className={navClassNames} role="navigation" aria-label="main navigation" ref={navRef}>
            <div className="navbar-brand">
                <Link className="navbar-item is-italic has-text-black-bis has-text-weight-bold" to="/">bit-dragon</Link>

                <a role="button" className="navbar-burger burger" aria-label="menu" aria-expanded="false" data-target="navbarBasicExample">
                    <span aria-hidden="true"/>
                    <span aria-hidden="true"/>
                    <span aria-hidden="true"/>
                </a>
            </div>
        </nav>
    );
};

export default NavigationComponent;
