import {useEffect} from "react";

export const useSummaryVisibility = (callback)  => {
    useEffect(() => {
        const $trigger = document.querySelector('.cv-summary')
        const observer = new  IntersectionObserver((entries) => {
            if (entries.length) {
                const entry = entries[0];
                const shouldHide = entry.intersectionRatio < .1;
                callback(shouldHide);
            }
        }, {threshold: [.1, .3]})
        if ($trigger) {
            observer.observe($trigger);
            return () => observer.unobserve($trigger);
        }
    }, [])
};
